.timeline {
    list-style: none;
    padding: 0;
    position: relative;
}

.timeline:before {
    content: '';
    position: absolute;
    top: 10px; 
    left: 6px; 
    height: calc(100% - 20px);
    width: 1px;
    background: #ffffff26; 
}

.timeline-item {
    position: relative;
    margin-bottom: 20px;
    padding-left: 40px; /* Desplazamos el contenido para dar espacio a la línea */
}

.timeline-item:before {
    content: '';
    position: absolute;
    top: 15%; /* Centramos verticalmente el circulito */
    left: 2px; /* Alineamos el circulito con la línea */
    transform: translateY(-50%); /* Ajuste fino para centrar */
    width: 9px; /* Tamaño más pequeño */
    height: 9px;
    border-radius: 50%;
    background: #55D3FB; /* Color del circulito */
}

.white-theme .timeline-item:before {
    background: #FF7733; /* Color del circulito */
}

.white-theme .timeline:before {
    background: #00000031; 
}

.timeline-item:first-child:before {
    top: 25px; /* Desplazamos el primer circulito un poco más abajo */
}

.timeline-content {
    padding: 10px 15px;
}

.timeline-content h3 {
    font-size: 1.25rem;
    margin-top: 0;
    margin-bottom: 0;
}

.timeline-content p {
    margin-bottom: 0;
}
