.header {
    display: flex;
    justify-content: center;
    width: 100%;
}

.web {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1;
    margin-top: 5px;
    top: 0;
}

.web nav {
    padding: 5px 15px;
    border: 2px solid #2c2c2c;
    background-color: #1A1A1D;
    color: #ffffff;
    font-size: 0.9rem;
    border-radius: 30px;
    transition: all 0.4s ease;
    outline: none;
    position: relative;
    overflow: hidden;
    font-weight: bold;
}

.white-theme .web nav{
  background-color: #14213D;  
}

nav a {
    text-decoration: none;
    color: white;
    margin: 0 15px;
    position: relative;
    transition: color 0.3s ease;
}

nav a:not(.active) {
    color: lightgray;
}

nav a::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background: white;
    left: 0;
    bottom: -5px;
    transform: scaleX(0);
    transition: transform 0.3s ease;
    transform-origin: left;
}

nav a:hover::after,
nav a.active::after {
    transform: scaleX(1);
}

nav a:hover {
    color: white;
}

.btn-change-theme {
    border: none;
    outline: none;
    background: none;
}

.mobile {
    display: none;
}

.mobile .bar {
    width: 25px;
    height: 3px;
    background-color: white;
    margin: 4px 0;
    transition: all 0.3s ease;
}

.white-theme .mobile .bar {
    background-color: #1A1A1D;

}



@media (max-width: 768px) {
    .header {
        position: fixed;
        padding: 8px 4px;
        z-index: 201;
        width: 50vw;
        left: 0;
    }

    nav a {
        display: flex;
        justify-content: center;
        
    }

    .white-theme nav a{
        color:black;
    }

    .white-theme nav a:not(.active) {
        color:rgb(65, 65, 65);
    }


    .white-theme nav a::after {
      background: #14213D;

    }

    .web {
        display: none;
    }

    .mobile {
        display: flex;
        align-items: center;
        justify-content: left;
        width: 100%;
        position: relative;
    }
    
    
    .mobile nav {
        display: flex;
        flex-direction: column;
        position: fixed;
        top: 0;
        left: -100%;
        width: 50vw;
        /* height: 100vh; */
        padding: 20px;
        padding-top: 40px;
        border-bottom-right-radius: 1.5rem;
        /* background:rgb(255, 255, 255); */
        background-color: #1A1A1D;
        border-right: 1px solid #cacaca;
        border-bottom:  1px solid #cacaca;
        /* background-image: radial-gradient(ellipse 80% 50% at 50% -10%, #0e1217, hsla(0, 0%, 2%, 0.514)); */
        background-image: radial-gradient(ellipse 80% 50% at 50% -20%, rgb(255, 254, 254), #ECDBBA);
        gap: 40px;
        
        transition: left 0.8s ease;
        z-index: 555;
    }
    
    .black-theme .mobile nav {
        background:rgb(255, 255, 255);
        background-image: radial-gradient(ellipse 80% 50% at 50% -10%, #001541, hsl(0, 0%, 2%));
        border-right: 1px solid #ffffff;
        border-bottom: 1px solid #ffffff;
    }
    .mobile nav.open {
        left: 0;
    }

    .hamburger {
        background: none !important;
        border: none;
        cursor: pointer;
        position: absolute;
        z-index: 600;
        top: 0px;
        right: calc(100% - 45px);
        transition: right 0.8s;
    }
    
    /* Estilos de la hamburguesa cuando está abierta */

    .hamburger.open{
        right: 0;
    }

    .hamburger.open .bar:nth-child(1) {
        transform: rotate(45deg) translate(5px, 5px);
    }
    
    .hamburger.open .bar:nth-child(2) {
        opacity: 0;
    }
    
    .hamburger.open .bar:nth-child(3) {
        transform: rotate(-45deg) translate(5px, -5px);
    }
}



/* From Uiverse.io by andrew-demchenk0 */ 
.switch {
    background: none;
    border: none;
    font-size: 17px;
    position: relative;
    display: inline-block;
    width: 64px;
    height: 34px;
  }
  
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #183153;
    
    transition: .4s;
    border-radius: 30px;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 30px;
    width: 30px;
    border-radius: 20px;
    left: 2px;
    bottom: 2px;
    z-index: 2;
    background-color: #e8e8e8;
    transition: .4s;
  }
  
  .sun svg {
    position: absolute;
    top: 6px;
    left: 5px;
    z-index: 1;
    width: 24px;
    height: 24px;
  }
  
  .moon svg {
    fill: #73C0FC;
    position: absolute;
    top: 5px;
    left: 36px;
    z-index: 1;
    width: 24px;
    height: 24px;
  }
  
  /* .switch:hover */.sun svg {
    animation: rotate 15s linear infinite;
  }
  
  @keyframes rotate {
   
    0% {
      transform: rotate(0);
    }
  
    100% {
      transform: rotate(360deg);
    }
  }
  
  /* .switch:hover */.moon svg {
    animation: tilt 5s linear infinite;
  }
  
  @keyframes tilt {
   
    0% {
      transform: rotate(0deg);
    }
  
    25% {
      transform: rotate(-10deg);
    }
  
    75% {
      transform: rotate(10deg);
    }
  
    100% {
      transform: rotate(0deg);
    }
  }
  
  .input:checked + .slider {
    background-color: #73C0FC;
  }
  
  
  .input:checked + .slider:before {
    transform: translateX(30px);
  }