
.avatar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px; /* Espacio debajo del contenedor del avatar */
}

.avatar {
  width: 150px; /* Ajusta el tamaño según sea necesario */
  height: 150px; /* Ajusta el tamaño según sea necesario */
  border-radius: 50%; /* Hace que la tarjeta sea circular */
  overflow: hidden; /* Oculta cualquier parte de la imagen que sobresalga */
  position: relative; /* Necesario para el efecto 3D */
  perspective: 1000px; /* Añade profundidad para el efecto de rotación */
}

.card-inner {
  width: 100%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.999s;
}

.avatar:hover .card-inner {
  transform: rotateY(180deg);
}

.card-front,
.card-back {
  border:1px solid #cacaca;
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 50%; /* Asegura que las caras también sean circulares */
  overflow: hidden; /* Asegura que las imágenes no sobresalgan */
}

.card-front {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  transform: rotateY(0deg);
}

.card-back {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  transform: rotateY(180deg);
}

/* Asegúrate de que las imágenes ocupen toda la tarjeta */
.avatar-front,
.avatar-back {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Mantiene la proporción de la imagen y la recorta si es necesario */
}
