.page-proyecto {
    display: flex;
    padding: 2rem;
    max-width: 1200px;
    width: 100%;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 4px 15px #f8ebd0;
    overflow: hidden;
}

.black-theme .page-proyecto {
    box-shadow: 0 4px 15px rgba(44, 44, 44, 0.5);
}

.col-video {
    display: flex;
    width: 100%;
    position: relative;
    margin: 10px 0;
}

.card-video {
    display: flex;
    gap: 1rem;
    border-radius: 1.5rem;
    padding: 1rem;
    width: 100%;
}

.card-imagenes {
    padding: 1rem;
    max-width: 100%;
    gap: 1rem;
    border: 1px solid #b4b4b4;
    border-radius: 1.5rem
}

.black-theme .card-imagenes {
    border: 1px solid #2c2c2c;
}

.project-videos {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;

}

.project-video {
    width: 100%;
    max-height: 700px;

}

.container-title {
    display: flex;
    justify-content: center;
    padding: 50px 0;
}

.project-title {
    display: flex;
    padding: 0 30px;
    align-items: center;
    gap: 30px;
    padding-bottom: 15px;
    border-bottom: 2px solid #2c2c2c;
}

.black-theme .project-title {
    border-bottom: 2px solid #b4b4b4;
}

.project-title img {
    max-width: 120px;
    border-radius: 12px;
}

.overlay {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.8);
}

.play-icon {
    position: relative;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}


.project-video:hover+.play-icon {
    pointer-events: auto;
    /* Permite clics en el ícono al pasar el mouse sobre el video */
}

.project-images figure {
    display: flex;
    flex-direction: row;
    /* Las imágenes y la descripción se colocarán una al lado de la otra */
    gap: 1rem;
    padding: 1rem;
    max-width: 100%;
}

.project-images img {
    width: 70%;
    max-width: 500px;
    transition: transform 0.3s ease-in-out;
    /* Añade una transición suave */
}

.project-images img:hover {
    transform: scale(1.3);
    /* Aumenta el tamaño de la imagen un 10% al pasar el mouse */
}


.project-images figcaption {
    display: flex;
    flex-direction: column;
    font-style: italic;
    color: #666;
    /* Por defecto, el título arriba y la descripción debajo */
}

/* Media query para pantallas menores a 1000px */
@media (max-width: 1000px) {
    .project-images figure {
        flex-direction: column-reverse;
        ;
        /* Coloca la imagen y el texto uno debajo del otro */
        align-items: center;
    }

    .project-images img {
        width: 100% !important;
        /* Haz que la imagen ocupe todo el ancho disponible */
    }

    .project-images figcaption {
        text-align: center;
        /* Centra el texto debajo de la imagen */
        margin-top: 1rem;
        /* Añade espacio entre la imagen y el texto */
    }

    .page-proyecto {
        padding: 1rem
    }
}


@media (max-width: 600px) {
    .page-proyecto {
        font-size: 14px;
    }
}