.main {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100vw;
  font-family: 'Arial', sans-serif; 
  font-family: "Source Sans 3", sans-serif;
  font-weight: 600;

}

/* WHITE THEME */
.main.white-theme {
  background-color: rgb(0, 0, 0);
  background-image: radial-gradient(ellipse 80% 50% at 50% -20%, rgb(255, 254, 254), #ECDBBA);
  color: #14213D;
}


/* BLACK THEME */
.main.black-theme {
  background-color: rgb(255, 255, 255);
  background-image: radial-gradient(ellipse 80% 50% at 50% -10%, #001541, hsl(0, 0%, 2%));
  color: white;
}

.main.black-theme p {
  color: rgb(209 213 219 /1);
}

.main.black-theme .highlight {
  color: #55D3FB!important;
  font-weight: 600;
}

.main.white-theme .highlight {
  color: #FCA311!important;
  font-weight: 600;
}

.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem;
}

.container-section {
  max-width: 650px;
  display: flex;
  flex-direction: column;
}

.section h1 {
  font-weight: bold;
  font-size: 32px;
}

.section h2 {
  font-weight: 600;
  width: 100%;
  margin-bottom: 15px;
}

/* Responsive styles */
@media (max-width: 1024px) {  
  .section h1 {
    font-size: 28px;
  }
  
  .section h2 {
    font-size: 24px;
  }
}

@media (max-width: 768px) {
  .section {
    padding: 3rem;
  }

  .container-section {
    max-width: 90%;
  }

  .section h1 {
    font-size: 24px;
  }
  
  .section h2 {
    font-size: 20px;
  }
}

@media (max-width: 480px) {
  .section {
    padding: 2rem;
  }

  .section h1 {
    font-size: 20px;
  }
  
  .section h2 {
    font-size: 18px;
  }
  
  .container-section {
    max-width: 100%;
  }
}
